.customers {
    @media screen and (max-width: 490px) {
        padding-top: 30px;
        padding-bottom: 30px;
    }
    &-wrapper {
        min-height: 145px;
        max-width: 945px;
    }
    &__company-item {
        width: 116px;
        height: 29px;
        fill: #B6BAC7;
    }
    &__company-item:hover {
        fill: #3A79FF;
    }
}